/* ================================= Form Css Start ================================= */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    -webkit-text-fill-color: hsl(var(--black)) !important;
    caret-color: hsl(var(--black));
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input[type=number] {
    -moz-appearance: textfield;
}

.form--label {
    @extend .fs-15;
    font-weight: 500;
    color: hsl(var(--black-two));
    margin-bottom: 8px;

    &.required::after {
        content: '*';
        display: inline-block;
        margin-left: 4px;
        color: hsl(var(--danger));
    }
}

.form-group {
    margin-bottom: 16px;
}

.select {
    color: hsl(var(--black) / .6) !important;

    &:focus {
        border-color: hsl(var(--base));
        color: hsl(var(--black)) !important;
    }

    option {
        background-color: hsl(var(--black));
        color: hsl(var(--white));
    }
}

.form--control {
    width: 100%;
    font-weight: 400;
    outline: none;
    border-radius: 8px;
    padding: 12.5px 16px;
    background-color: #F8F8F8;
    border: 1px solid hsl(var(--black) / .07);
    color: hsl(var(--black)/.8);
    line-height: 1;

    @include xsm-screen {
        padding: 9px 16px;
    }

    &-sm {
        padding: 7.5px 16px;

        @include xsm-screen {
            padding: 4px 16px;
        }
    }

    &-lg {
        padding: 17.5px 16px;

        @include xsm-screen {
            padding: 14px 16px;
        }
    }

    &::placeholder {
        color: hsl(var(--black)/.8);
    }

    &:focus {
        border-color: hsl(var(--base));
    }

    &:disabled,
    &[readonly] {
        background-color: hsl(var(--black)/.2);
        opacity: 1;
        border: 0;
    }

    &[type="file"] {
        line-height: 50px;
        padding: 0;
        position: relative;

        &::file-selector-button {
            border: 1px solid hsl(var(--black) / .08);
            padding: 4px 6px;
            border-radius: .2em;
            background-color: hsl(var(--base)) !important;
            transition: .2s linear;
            line-height: 25px;
            position: relative;
            margin-left: 15px;
            color: hsl(var(--body-color)) !important;

            &:hover {
                background-color: hsl(var(--base));
                border: 1px solid hsl(var(--base));
                color: hsl(var(--black));
            }
        }
    }
}

select.form--control {
    height: 48px;

    @include xsm-screen {
        height: 41px;
    }
}

select.form--control-sm {
    height: 38px;

    @include xsm-screen {
        height: 31px;
    }
}

select.form--control-lg {
    height: 58px;

    @include xsm-screen {
        height: 51px;
    }
}

textarea.form--control {
    height: 130px;
}

.form--check {
    @extend .flex-wrap;
    align-items: center;

    .form-check-input {
        box-shadow: none;
        background-color: transparent;
        box-shadow: none !important;
        border: 0;
        position: relative;
        border-radius: 4px;
        width: 16px;
        height: 16px;
        border: 1px solid hsl(var(--black)/.2);
        margin-top: 0px;
        cursor: pointer;

        &:checked {
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--base)) !important;
            box-shadow: none;

            &[type=checkbox] {
                background-image: none;
            }

            &::before {
                position: absolute;
                content: "\f00c";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                color: hsl(var(--white));
                @extend .fs-11;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    .form-check-label {
        width: calc(100% - 16px);
        @extend .fs-14;
        font-weight: 500;
        padding-left: 12px;
        cursor: pointer;

        a {
            display: inline-block;
        }
    }
}

.form--radio {
    @extend .flex-wrap;
    align-items: center;

    .form-check-input {
        box-shadow: none;
        border: 1px solid hsl(var(--black)/.2);
        position: relative;
        background-color: transparent;
        cursor: pointer;
        width: 16px;
        height: 16px;

        &:active {
            filter: brightness(100%);
        }

        &:checked {
            background-color: transparent;
            border-color: hsl(var(--base));

            &[type=radio] {
                background-image: none;
            }

            &::before {
                position: absolute;
                content: "";
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                width: 7px;
                height: 7px;
                background-color: hsl(var(--base));
                border-radius: 50%;
                z-index: 999;
            }
        }
    }

    .form-check-label {
        width: calc(100% - 16px);
        @extend .fs-15;
        font-weight: 500;
        padding-left: 12px;
        cursor: pointer;

        a {
            display: inline-block;
        }
    }
}

.form--switch {
    .form-check-input {
        border-radius: 3px;
        background-image: none;
        position: relative;
        box-shadow: none;
        border: 0;
        background-color: hsl(var(--black)/.2) !important;
        padding: 10px !important;
        margin-left: 0;
        margin-bottom: 5px;
        border-radius: 40px;
        width: 70px;
        height: 38px;
        cursor: pointer;

        &:focus {
            border-radius: 40px;
            background-image: none;
            position: relative;
            box-shadow: none;
            border: 0;
        }

        &::before {
            position: absolute;
            content: "";
            width: 28px;
            height: 28px;
            background-color: hsl(var(--white));
            top: 50%;
            transform: translateY(-50%);
            border-radius: 2px;
            left: 5px;
            border-radius: 50%;
            transition: 0.2s linear;
        }

        &:checked {
            background-color: hsl(var(--base)) !important;

            &::before {
                left: calc(100% - 33px);
                background-color: hsl(var(--white)) !important;
            }
        }

        &:checked[type=checkbox] {
            background-image: none;
        }
    }

    .form-check-label {
        width: calc(100% - 14px);
        padding-left: 5px;
        cursor: pointer;
    }
}

.input-group--custom {
    display: flex;
    border: 1px solid hsl(var(--base)/.32);
    border-radius: 8px;
    overflow: hidden;
    
    &:focus-within {
        border-color: hsl(var(--base));
    }

    &:focus-within .btn--group {
        border-left-color: hsl(var(--base));
    }

    .form--control {
        border-width: 0px;
        border-radius: 0px;
        padding-top: 14.5px;
        padding-bottom: 14.5px;
        font-weight: 600;
        color: hsl(var(--gray-two));
    }

    .btn--group {
        border-left: 1px solid hsl(var(--base)/.32);
        background-color: #F8F8F8;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
        display: flex;
        align-items: center;
        padding-left: 16px;
        padding-right: 16px;

        button {
            @include fs-14;
            font-weight: 600;
            color: hsl(var(--base));
            line-height: 16px;
            white-space: nowrap;
        }

        .divider{
            width: 1px;
            height: 14px;
            background-color: hsl(var(--base)/.55);
            display: inline-block;
            margin: 0 20px;

        }
    }
}

.input-group--copy {
    flex-wrap: nowrap;
    border: 1px solid hsl(var(--base-l-900));
    border-radius: 8px;
    overflow: hidden;
    
    &:focus-within {
        border-color: hsl(var(--base));
    }

    .form--control {
        border-color: transparent;
        background-color: transparent;
        color: hsl(var(--gray-two));
    }
    
    .btn {
        border-top-left-radius: 8px !important;
        border-bottom-left-radius: 8px !important;
    }
}

.input-group.input-with-text {
    flex-wrap: nowrap;
}

.input-with-text .input-group-text {
    color: hsl(var(--black)/.8);
    border: 1px solid hsl(var(--black) / .07);
    background-color: #F8F8F8 !important;
    border-radius: 8px;
}

input#your-password,
input#confirm-password {
    padding-right: 50px;
}

.password-show-hide {
    position: absolute;
    right: 20px;
    z-index: 5;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: hsl(var(--black)/.4);
}

.range-slider {

    &__slide {
        border-width: 0px !important;
        background-color: hsl(var(--base)/.15);
        margin-bottom: 28px;
    }

    .ui-slider-horizontal {
        height: 3px;
    }

    .ui-widget-header {
        background-color: hsl(var(--base));
    }

    .ui-slider-handle {
        width: 16px;
        height: 16px;
        border-radius: 50%;
    }

    .ui-state-default,
    .ui-widget-content .ui-state-default,
    .ui-widget-header .ui-state-default,
    .ui-button,
    html .ui-button.ui-state-disabled:hover,
    html .ui-button.ui-state-disabled:active {
        background-color: hsl(var(--base)) !important;
        border-color: hsl(var(--base)) !important;
        outline: none;

    }

    &__inputs {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .input-group {
            width: 40%;
            flex-wrap: nowrap;
        }

        .input-group-text {
            color: hsl(var(--gray));
            border-color: hsl(var(--base));
            background-color: transparent;
            border-radius: 4px;
            padding: 0px 6px;
        }
        
        .form--control {
            font-weight: 500;
            border: 1px solid hsl(var(--base)) !important;
            background-color: transparent !important;
            padding-top: 6px;
            padding-bottom: 6px;
            border-radius: 4px;
        }
    }

}

/* ================================= Form Css End =================================== */